@import 'general';
@import 'variables';
@import 'mixins';
@import 'keyframes';
@import 'fancybox';

// .loading {
//   filter: none !important;
//   background: red;
// }

// ! PAGE LAYOUT
.content__section {
  &--nbpIntro {
    // Just a simple block
  }
  &--gallery {
    @include gallery(
      repeat(auto-fit, minmax(12rem, 1fr)),
      auto,
      $space-m,
      15rem
    );
    @media screen and (max-width: $breakpoint-m) {
      @include gallery(repeat(2, minmax(12rem, 1fr)), auto, $space-m, 15rem);
    }
    @media screen and (max-width: $breakpoint-s) {
      @include gallery(repeat(1, minmax(12rem, 1fr)), auto, $space-s, 15rem);
    }
  }
}

// ! SECTION LAYOUT
// ! Photography Intro
.nbpIntro {
  &__text {
    @include columns(3, $space-m, 0.01rem dashed $mid2-color);
    text-align: justify;
    @media screen and (max-width: $breakpoint-m) {
      @include columns(2, $space-s, 0.01rem dashed $mid2-color);
    }
    @media screen and (max-width: $breakpoint-s) {
      @include columns(1, 0, 0 dashed $mid2-color);
    }
    img {
      width: 100%;
      margin: 0 auto;
      filter: drop-shadow(0 0.25rem 0.5rem rgba(0, 0, 0, 0.25));
    }
  }
}
