@import 'base';
@import 'variables';
@import 'fonts';
@import 'mixins';
@import 'keyframes';
@import 'lazyload';
@import 'utilities';
$aos-distance: 30px;
@import '../../node_modules/aos/src/sass/aos.scss';

// ! ================= General Element Styles =========================

// Hide vertical scroll bars

html {
	overflow: scroll;
	overflow-x: hidden;
	overflow-y: scroll;
	scroll-behavior: smooth;
	-webkit-overflow-scrolling: touch;
	background: $dark-color;
	position: relative;
	z-index: 0;
	@media print {
		overflow: auto;
	}
}
::-webkit-scrollbar {
	width: 0px; /* Remove scrollbar space */
	background: transparent; /* Optional: just make scrollbar invisible */
}

// Change selection color

::selection {
	background-color: $accent-color;
	color: $dark-color;
	text-shadow: none;
}

body {
	@include linearGradientBG(
		$dark-color,
		0%,
		$mid2-color,
		100%,
		180deg
	);
	font-family: $text-font;
	font-weight: 400;
	color: $bright-color;
	z-index: 1;
	background-attachment: fixed;
	background-size: cover;
	background-position: center;
	font-size: $font-size-m;
}

#particles-js {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 3;
	overflow: hidden;
	@media screen and (max-width: $breakpoint-m) {
		width: 100vw;
	}
	@media print {
		display: none;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	@include textShadow();
	font-family: $header-font;
	color: $mid1-color;
	display: block;
	width: fit-content;
	padding: $space-m 0;
}

a {
	@include linkStyle();
}

p {
	@include textShadow();
	line-height: $line-height;
}
strong {
	letter-spacing: 0.05rem;
	font-weight: 600;
}

// ! ================= Cross Page Styles ==============================

// ================= / Header =======================================
.header {
	@include flex(row nowrap, center, center, center);
	background: transparent;
	transition: all calc(#{$trans-fast} / 2);
	width: 100%;
	height: $header-height;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1000;
	@media screen and (max-width: #{$breakpoint-m}) {
		display: block;
		@include boxShadow--tight();
	}
	&__innerWrapper {
		@include flex(row nowrap, space-between, center, center);
		transition: all calc(#{$trans-fast} / 2);
		width: $wrapper-width--normal;
		height: 100%;
		padding: 0 $wrapper-padding--l;
		@media screen and (min-width: $breakpoint-xxl) {
			width: $wrapper-width--narrower;
		}
		@media screen and (min-width: $breakpoint-xxxl) {
			width: $wrapper-width--narrowest;
		}
		@media screen and (max-width: $breakpoint-xl) {
			width: $wrapper-width--widest;
		}
		@media screen and (max-width: $breakpoint-l) {
			width: $wrapper-width--full;
		}
		@media screen and (max-width: $breakpoint-m) {
			width: $wrapper-width--full;
			padding: 0 $wrapper-padding--m;
		}
		@media screen and (max-width: $breakpoint-s) {
			padding: 0 $wrapper-padding--s;
		}
	}
	&__logo {
		@include flex(row nowrap, center, center, center);
		font-family: $text-font;
		color: $bright-color;
		height: 100%;
		user-select: none;
		font-size: $font-size-m;
		font-weight: 600;
		transition: border-bottom $trans-fast;
		.logo {
			&__ni {
				font-weight: 400;
			}
			&__bru,
			&___ {
				transform: translateY(0.5px);
			}
			&__bru {
				font-weight: 600;
			}
			&___ {
				color: $accent-color;
			}
		}
	}
	&__mobileNavBtn {
		display: none;
		@media screen and (max-width: $breakpoint-m) {
			@include flex(row nowrap, flex-end, center, center);
			height: 100%;
			width: 15%;
			color: $bright-color;
		}
	}
}

.headerTransparentBG {
	background: transparent;
}

.headerSolidBG {
	background: $dark-color;
}

.headerBoxShadow {
	@include boxShadow--tight();
}

// ! ================= / Nav ==========================================
.nav {
	&--desktop {
		@include flex(row nowrap, center, center, center);
		height: 100%;
		@media screen and (max-width: $breakpoint-m) {
			display: none;
		}
	}
	&--mobile {
		display: none;
		@media screen and (max-width: $breakpoint-m) {
			@include flex(column nowrap, flex-start, center, center);
			@include linearGradientBG(
				$dark-color,
				0%,
				$mid2-color,
				100%,
				180deg
			);
			position: fixed;
			top: $header-height;
			padding: $space-xs;
			width: 100vw;
			height: calc(100vh - #{$header-height});
			transform: translateY(calc(150vh - #{$header-height}));
			transition: transform $trans-fast;
			z-index: 15;
		}
	}
	&--mobile--opened {
		padding-top: $space-m;
		transform: translateY(0);
	}
	&__item {
		&--desktop {
			@include flex(row nowrap, center, center, center);
			@include navItem--desktop();
			color: $bright-color;
			height: 100%;
			&.active {
				border-bottom: 3px solid $accent-color;
				color: $accent-color;
			}
		}
		&--mobile {
			font-size: $font-size-l;
			margin-bottom: $space-m;
			color: $bright-color;
			font-weight: 400;
		}
	}
}

#scrollToTopBtn {
	@include boxShadow--std(0.25);
	position: relative;
	z-index: 1000;
	box-sizing: border-box;
	display: inline-block;
	background: $dark-color;
	line-height: 1rem;
	font-size: $font-size-l;
	color: $bright-color;
	border-radius: 50%;
	position: fixed;
	bottom: 1rem;
	right: 1rem;
	text-align: center;
	outline: 0;
	padding: 1rem;
	border: 0.25rem solid transparent;
	transition: all $trans-fast;
	span {
		position: relative;
		left: -1px;
		top: -2px;
	}
	&:hover {
		border: 0.25rem solid $accent-color;
		@media screen and (max-width: $breakpoint-m) {
			border: none;
		}
	}
}

.scrollToPoint {
	display: block;
	position: relative;
	visibility: hidden;
	top: calc((#{$header-height} + #{$scrollToMenu-height}) * -1);
	@media screen and (max-width: $breakpoint-m) {
		top: $header-height;
	}
	@media screen and (max-width: $breakpoint-s) {
		top: $header-height;
	}
}

// ! ================= / Wrapper ======================================
#wrapper {
	background: transparent;
	width: $wrapper-width--normal;
	position: relative;
	z-index: 10;
	margin: 0 auto;
	padding: $wrapper-padding--l;
	border-radius: $border-radius;
	@media screen and (min-width: $breakpoint-xxl) {
		width: $wrapper-width--narrower;
	}
	@media screen and (min-width: $breakpoint-xxxl) {
		width: $wrapper-width--narrowest;
	}
	@media screen and (max-width: $breakpoint-xl) {
		width: $wrapper-width--widest;
	}
	@media screen and (max-width: $breakpoint-l) {
		width: $wrapper-width--full;
	}
	@media screen and (max-width: $breakpoint-m) {
		width: $wrapper-width--full;
		margin: $header-height auto 0;
		padding: 0;
		border-radius: 0;
	}
	@media screen and (max-width: $breakpoint-s) {
		width: $wrapper-width--full;
		border-radius: 0;
		box-shadow: none;
	}
}

// ================= / Content ======================================
.content {
	&__header {
		display: block;
		h1,
		h2 {
			margin: 0 0 0 0;
			text-align: left;
			width: 100%;
			font-size: $font-size-xl;
			padding: $space-s 0;
			color: $bright-color;
			letter-spacing: 0.15rem;
			// text-transform: uppercase;
			@media screen and (max-width: $breakpoint-m) {
				padding: $space-xs 0;
			}
		}
		@media screen and (max-width: $breakpoint-m) {
			@include sticky(14);
			padding: $space-xs $wrapper-padding--m;
		}
		@media screen and (max-width: $breakpoint-s) {
			padding: 0 $wrapper-padding--s;
		}
	}
	&__subheader {
		page-break-before: always;
		display: inline-block;
		font-size: $font-size-l;
		margin: $space-m 0 $space-s 0;
		padding: 0;
		color: $bright-color;
		text-align: left;
		width: 100%;
		@media screen and (max-width: $breakpoint-s) {
			padding: 0 $wrapper-padding--s;
		}
	}
	&__description {
		width: 100%;
		text-align: left;
		margin: 0 0 $space-m 0;
		@media screen and (max-width: $breakpoint-s) {
			padding: $space-s $wrapper-padding--s;
		}
	}
	&__section {
		margin: 0 0 $space-m;
		padding: 0 0 $space-m 0;
		@media screen and (max-width: $breakpoint-m) {
			padding: $space-s $wrapper-padding--m;
		}
		@media screen and (max-width: $breakpoint-s) {
			// padding: $space-m $wrapper-padding--s;
			padding: $space-m 0;
		}
	}
}

// ================= / Footer =======================================
.footer {
	margin-top: $space-m;
	padding-bottom: $space-m;
	@media print {
		display: none;
	}
	&__wrapper {
		@include flex(row nowrap, center, center, center);
	}
	&__legal {
		text-align: center;
		padding: $space-s 0 0;
		&__link {
			color: $bright-color;
			font-size: $font-size-m;
			&:hover {
				background: transparent;
				color: $accent-color;
			}
		}
	}
	&__link {
		font-size: $font-size-l;
		margin: $space-s $space-s 0;
		transition: calc(#{$trans-fast} / 4);
		&--instagram {
			color: #e1306c;
			&:hover {
				color: #fcaf45;
			}
		}
		&--xing {
			color: #cfdc00;
			&:hover {
				color: #026466;
			}
		}
		&--github {
			color: #fafafa;
			&:hover {
				color: #333;
			}
		}
		&--twitter {
			color: #1da1f2;
			&:hover {
				color: #e1e8ed;
			}
		}
	}
}
